min-height
----------------------------------------
usage:
  .minh-[key]
----------------------------------------
output:
  min-height: [value];
----------------------------------------
example:
  .minh-card {
    min-height: 200px; }
----------------------------------------
*/
/*
========================================
min-width
----------------------------------------
usage:
  .minw-[key]
----------------------------------------
output:
  min-width: [value];
----------------------------------------
example:
  .minw-desktop {
    min-width: 960px; }
----------------------------------------
*/
/*
========================================
opacity
----------------------------------------
usage:
  .opacity-[key]
----------------------------------------
output:
  opacity: [value];
----------------------------------------
example:
  .opacity-0 {
    opacity: 0; }
----------------------------------------
*/
/*
========================================
outline
----------------------------------------
sets width and style:solid
----------------------------------------
usage:
  .outline-[modifier]
----------------------------------------
output:
  outline: [modifier] solid;
----------------------------------------
example:
  .outline-g05 {
    outline: 4px solid }
----------------------------------------
*/
/*
========================================
outline-color
----------------------------------------
usage:
  .outline-color-[key]
----------------------------------------
output:
  outline-color: [value];
----------------------------------------
example:
  .outline-color-yellow-50 {
    outline-color: #91721f; }
----------------------------------------
*/
/*
========================================
overflow
----------------------------------------
usage:
  .overflow-[modifier]
----------------------------------------
output:
  overflow[-modifier]: [value] solid;
----------------------------------------
example:
  .overflow-y-hidden {
    overflow-y: hidden; }
----------------------------------------
*/
/*
========================================
order
----------------------------------------
usage:
  .order-[key]
----------------------------------------
output:
  order: [value]
----------------------------------------
example:
  .order-first {
    order: -1; }

  .order-2 {
    order: 2; }
----------------------------------------
*/
/*
========================================
padding
----------------------------------------
usage:
  .padding-[modifier]*-[value]
----------------------------------------
output:
  padding: [value]
----------------------------------------
example:
  .padding-0 {
    padding: none; }

  .padding-bottom-1px {
    padding-bottom: 1px; }
----------------------------------------
*/
/*
========================================
PIN
----------------------------------------
usage:
  .pin-all
----------------------------------------
output:
  bottom: 0
  left:0
  right: 0
  top: 0
----------------------------------------
example:
  .pin-all
----------------------------------------
*/
/*
========================================
position
----------------------------------------
usage:
  .position-[key]
----------------------------------------
output:
  position: [value];
----------------------------------------
example:
  .position-absolute {
    position: absolute; }
----------------------------------------
*/
/*
========================================
.right
----------------------------------------
property: right
----------------------------------------
usage:
  .right-[key]
----------------------------------------
output:
  right: [value];
----------------------------------------
example:
  .right-n2px {
    right: -2px; }
----------------------------------------
*/
/*
========================================
SQUARE
----------------------------------------
usage:
  .square-[key]
----------------------------------------
output:
  height: [value];
  width: [value];
----------------------------------------
example:
  .square-g6 {
    height: 3rem;
    width: 3rem; }
----------------------------------------
*/
